import React from "react";
import PropTypes from "prop-types";

function RImage({ ready }) {

    if (!ready) {
        return <div>Script not loaded, unable to display rotated image</div>
    }

    return (
        <div id="first-medal" 
            className="cloudimage-360"
            data-folder="assets/img/main-medal/"
            data-filename="covid_frame_00{index}.jpg"
            data-amount="36"
            data-autoplay="true"
            data-speed="175"
            data-hide-360-logo="true"
            spin-number="2"
        /> 
    )
}

RImage.propTypes = {
    ready: PropTypes.bool,
}

export default RImage;